.navbar {
  background-color: #000;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  width: 100%;
  height: auto;
}
.logo img {
  padding: 18px;
  width: 50px;
  -webkit-animation: roll 1s infinite;
          animation: roll 1s infinite;
}
.nav button {
  background-color: black;
  border: none;
  color: white;
  padding: 20px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  margin-top: 10px;
  transition: 0.2s;
  font-family: "Indie Flower", cursive;
}
.nav button:hover {
  color: black;
  background-color: pink;
  border-radius: 3px;
}
@media only screen and (max-width: 785px) {
  [className*="navbar"] {
    width: 100%;
  }
}
@-webkit-keyframes roll {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes roll {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logofooter {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 6% 0;
}
.logofooter {
  -webkit-flex: 1 1;
