  }
}

.allslide {
  width: 100%;
}
.slide {
  position: relative;
  margin: 0 auto;
  width: 620px;
  height: 490px;
  overflow: hidden;
}

.slidertitle {
  font-size: 50px;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
  color: white;
  font-family: "Indie Flower", cursive;
}

.slide input {
  position: absolute;
  width: 960px;
  height: 460px;
  left: 0;
  top: 0;
  opacity: 0;
  cursor: pointer;
  margin: 0;
  padding: 0;
  width: 99%;
}

.slide img {
  visibility: hidden;
  opacity: 0;
  height: 450px;
  position: absolute;
  left: 0;
  top: 0;
  border: 5px solid #ddd;
  border-radius: 20px;
  transition: all 0.5s ease-out;
}

.slide input:checked + img {
  visibility: visible;
  opacity: 1;
}

.slide input:checked {
  display: none;
}

.slide input:nth-child(1) {
  z-index: 10;
}
.slide input:nth-child(3) {
  z-index: 11;
}
.slide input:nth-child(5) {
  z-index: 12;
}

.slide input:nth-child(1):checked ~ input:nth-child(3),
.slide input:nth-child(3):checked ~ input:nth-child(5),
.slide input:nth-child(5):checked ~ input:nth-child(7) {
  z-index: 15;
}
@media only screen and (max-width: 785px) {
  [className*="allslide"] {
    width: 100%;
  }
}

